<template>
  <div class="contact">
    <Navbar />
    <div class="banner">
      <div class="content">
        <div class="banner-t">
          <div class="banner-title">
            <h1>联系我们</h1>
            <h2>Contact</h2>
          </div>
          <p class="paragraph ffd">
            业务体系涵盖自然资源、数字中国，以及大数据和云服务等信息化平台应用，
            为国内外海量用户提供有竞争力的智慧成功案例、产品和服务，
            并致力于打造智能共享的信息化传递方式，构建资源共享的数据生态格局
          </p>
        </div>
        <div class="bar">
          <el-row class="bar-list">
            <el-col :span="12">
              <div
                class="list-item"
                :class="{ active: activeTab == 'tab1' }"
                @click="findDetail('tab1')"
              >联系方式</div>
            </el-col>
            <el-col :span="12">
              <div
                class="list-item"
                :class="{ active: activeTab == 'tab2' }"
                @click="findDetail('tab2')"
              >招聘岗位</div>
            </el-col>
          </el-row>
        </div>
      </div>
    </div>
    <div class="content">
      <section class="section section1" v-show="activeTab == 'tab1'">
        <el-row>
          <el-col :span="12">
            <el-card :body-style="{'padding': 0}">
              <div id="map"></div>
              <div class="map-tip" @click="openMap">
                <img src="../assets/img/dot.png" alt />
                <span>公司地址：贵州省贵阳市观山湖区德福中心A4栋17楼</span>
                <el-tooltip class="item" effect="dark" content="点击查看大图" placement="top">
                  <i class="icon el-icon-full-screen"></i>
                </el-tooltip>
              </div>
            </el-card>
          </el-col>
          <el-col :span="12"></el-col>
        </el-row>
      </section>
      <section class="section section1" v-show="activeTab == 'tab2'">岗位</section>
    </div>
    <Footerbar />
  </div>
</template>

<script>
import dot from "../assets/img/dot.png";

import Navbar from "@/components/navbar.vue";
import Footerbar from "@/components/footerbar.vue";

export default {
  name: "contact",
  data() {
    return {
      map: null,
      activeTab: "",

      mapUrl:
        "https://ditu.amap.com/search?id=B0FFL6O2IK&city=520115&geoobj=106.645492%7C26.609441%7C106.653328%7C26.614724&query_type=IDQ&query=%E5%BE%B7%E7%A6%8F%E4%B8%AD%E5%BF%83A4&zoom=17.5"
    };
  },
  components: {
    Navbar,
    Footerbar
  },
  created() {
    this.activeTab = `tab${this.$route.params.id ? this.$route.params.id : 1}`;
  },
  mounted() {
    this.map = new AMap.Map("map", {
      zoom: 14,
      minZoom: 1,
      center: [106.64691567, 26.61243339],
      resizeEnable: true
    });

    const marker = new AMap.Marker({
      position: new AMap.LngLat(106.64691567, 26.61243339),
      icon: dot,
      offset: new AMap.Pixel(-2, -32)
    });

    this.map.add(marker);

    this.map.on("complete", function() {
      document.getElementsByClassName("amap-copyright")[0].style.opacity = 0;
    });
  },
  methods: {
    findDetail(v) {
      this.activeTab = v;
    },

    openMap() {
      window.open(this.mapUrl, "_blank");
    }
  }
};
</script>

<style scoped lang="scss">
.banner {
  background: url("../assets/img/bg11.png") center top/cover no-repeat;
}
.content {
  margin-bottom: 50px;
  .section {
    width: 1200px;
    margin: 0 auto;
    padding-top: 30px;
  }
}

#map {
  width: 600px;
  height: 500px;
}

.map-tip {
  height: 50px;
  line-height: 50px;
  padding: 0 15px;
  font-size: 15px;
  color: #fff;
  background: linear-gradient(#58d6ca, #4db6ac);
  // background: linear-gradient(#14b4f1, #0288d1);
  background-color: #4db6ac;
  letter-spacing: 1.5px;

  .icon {
    float: right;
    margin-top: 15px;
    font-size: 25px;
    cursor: pointer;
  }

  img {
    width: 22px;
    margin-right: 10px;
    vertical-align: sub;
  }
}
</style>